          prevEl: ".swiper-button-prev",
          nextEl: ".swiper-button-next",
        },
        slidesPerView: 2,
        centeredSlides: true,
        autoPlay: true,
        loop: true,
        autoplay: {
          delay: 2500,
          disableOnInteraction: false,
        },
        speed: 1000,
        breakpoints: {
          320: {
            slidesPerView: 1,
            spaceBetween: 0,
          },
          640: {
            slidesPerView: 1,
            spaceBetween: 0,
          },
          767: {
            slidesPerView: 1,
            spaceBetween: 0,
            centeredSlides: false,
          },
          991: {
            slidesPerView: 2,
          },
        },
      },
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
};
